import React,{Component,propTypes} from 'react';
import {connect} from 'react-redux';
var $  = require( 'jquery' );
var DataTable = require( 'datatables.net' );
require( 'datatables.net-dt/css/jquery.dataTables.css' );
import {QueryData,setQueryParam} from './Action';
class App extends Component{
	constructor(props){
		super(props);
		this.state={
			searchContent:''
		}
		this.$datatable ={};
	}
	componentDidMount(){
		let self =this;
		$.get(self.props.config.backend,function(data){
			console.log(data);
			self.props.onQuery(data);
			self.paintTable();
			
		});
	}
	/**
	 * 
	 */
	paintTable(){
		let self = this;
		self.$datatable =$(".main-tb").DataTable({
		"data":self.props.data, //
		  "retrieve":true,//
		 "columns": [ //
        { "data": "_id" ,"title":"id"},
        { "data": "name","title":"" },
        { "data": "age","title":"" }
    	],
    	"lengthMenu": [ 5, 10, 15, 20, 100 ],
    	 "pagingType": "full_numbers",
    	"language":{
    		"emptyTable":"@_@",
    		"info":" _START_  _END_ _TOTAL_ ",
    		"infoEmpty":"0",
    		"infoFiltered":"(filtered from _MAX_ total entries)",
		    "infoPostFix":    "",
		    "thousands":",",
		    "lengthMenu": " _MENU_ ",
		    "loadingRecords": "...",
		    "processing":     "...",
		    "search":         ":",
		    "zeroRecords":    "",
		    "paginate": {
		        "first":      "",
		        "last":       "",
		        "next":       "",
		        "previous":   ""
		    },
		    "aria": {
		        "sortAscending":  ": activate to sort column ascending",
		        "sortDescending": ": activate to sort column descending"
		    }
    	}
		});
		self.$datatable.draw();
	}
	queryDataTables(){
		let self = this;
		
		$.get(self.props.config.backend,{name:self.props.searchContent},function(data){
			console.log(data);
			self.props.onQuery(data);
			self.$datatable.destroy();
			self.paintTable();
			
		});
	}
	updateUser(){
		let self = this;
		$.get("/components/updateData",{},function(data){
			alert(data);

		});
	}
	addUser(){
		let self = this;
		$.get("/components/addData",{},function(data){
			alert(data);

		});
	}
	handleInput(event){
		let self = this;
		self.props.handleInput(event.target.value);
	
	}
	render(){
		let self = this;
		return (
			<div className="dtable">
			<input type="text" value={self.props.searchContent} onChange={self.handleInput.bind(this)} className="search"/>
			<button onClick={self.queryDataTables.bind(this)}></button>
			<table className="main-tb">
			</table>

			<button onClick={self.updateUser.bind()}></button>
			<button onClick={self.addUser.bind()}></button>
			
			</div>
			)
	}
}
//  Redux  state  props 
function mapStateToProps(state) {
  return {
    data: state.data,
    searchContent:state.searchContent
  };
}
//  action  props 
function mapDispatchToProps(dispatch) {
  return {
    onQuery: (data) => dispatch(QueryData(data)),
    handleInput:(queryText) =>dispatch(setQueryParam(queryText))
  };
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);


